@keyframes moveRight {
  0% {
    left: 45%;
    opacity: 0;
  }
  50% {
    left: 55%;
    opacity: 1;
  }
  100% {
    left: 50%;
  }
}
@keyframes moveLeft {
  0% {
    right: 55%;
    opacity: 0;
  }
  50% {
    left: 45%;
    opacity: 1;
  }
  100% {
    left: 50%;
  }
}
@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
*,
*::before,
*::after {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
}

body {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif !important;
}

.container {
  max-width: 73.75em;
  margin: 0 auto;
}

.mt-3 {
  margin-top: 1.25em;
}

@font-face {
  font-family: "Roboto";
  src: url(/font/Roboto-Regular.woff) format("woff");
  font-weight: 400;
}
li {
  list-style: none;
}

ul {
  margin: 0 !important;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  font-weight: 600;
  padding: 0.9375em 1.875em;
  border-radius: 0.9375em;
  border: none;
  text-transform: uppercase;
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  will-change: transform;
  box-shadow: 0.125em 0.375em 0.625em rgba(51, 51, 51, 0.15);
}
.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0.125em 0.75em 0.625em rgba(51, 51, 51, 0.15);
}
.btn--color {
  background-color: #f67842;
  color: #fff;
}
.btn:active {
  transform: scale(0.97);
}
.btn:focus {
  outline: transparent;
}

.nav-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  height: 6.25em;
}

.site-header {
  padding-top: 0 !important;
  margin: 0 !important;
  border-bottom-color: transparent !important;
}

.dropdown {
  position: relative;
  display: inline-block;
  transition: all 3s;
}
.dropdown__link {
  color: #333;
  transition: 0.3s;
}
.dropdown__link:hover {
  color: orange;
}
.dropdown:hover .dropdown__content {
  display: block;
}
.dropdown__content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 10em;
  box-shadow: 0px 8px 1em 0px rgba(0, 0, 0, 0.2);
  padding: 0.75em 1em;
  z-index: 1;
  transition: all 3s;
  border-radius: 5px;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-item a {
  font-size: 0.875em;
  font-weight: 600;
  text-transform: uppercase;
  transition: 0.3s;
  font-weight: 500;
  color: #333;
}
.menu-item a:hover {
  color: orange;
}

.menu-header-menu li a {
  margin: 6.25em 0;
}

.menu-header-menu-container .menu .menu-item:not(:last-child) {
  padding-right: 0.625em;
}

.footer-box {
  display: flex;
  justify-content: center;
}

.section-frontpage {
  background-image: url(../src/images/blue.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 80vh;
  position: relative;
  border-radius: 40px;
  margin: 0 30px;
}

.text-box {
  text-align: center;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100%;
}

